<template>
    <div>
        <el-card shadow="always">
            <div slot="header" class="horiz-container vertical-center">
                <span class="header-text">论坛主题管理</span>
                <el-divider direction="vertical" class="header-text"></el-divider>
                <el-button
                    type="danger"
                    :disabled="selected.length <= 0"
                    @click="deleteSelected"
                >批量删除</el-button>
                <el-button
                    type="primary"
                    :disabled="selected.length <= 0"
                    @click="resetSelectedLevel"
                >重置选中的排序值</el-button>
                <div class="spacer"></div>
                <el-button size="medium" type="success" @click="createItem">
                    <i class="fa fa-plus"></i>
                    添加新主题
                </el-button>
            </div>
            <el-table
                :data="topices"
                v-loading="loading"
                @selection-change="mutilSelectionChange"
                style="width: 100%"
                stripe
                border
            >
                <el-table-column type="selection" width="35" align="center"></el-table-column>
                <el-table-column prop="category.label" label="版块"></el-table-column>
                <el-table-column prop="title" label="主题"></el-table-column>
                <el-table-column prop="level" label="置顶值" width="70"></el-table-column>
                <el-table-column label="查看/评论" width="100" align="center">
                    <template slot-scope="scope">
                        <span>{{ scope.row.views }}</span>
                        /
                        <span>{{ scope.row.reply }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="作者" width="160">
                    <template slot-scope="scope">
                        <span>{{ scope.row.createdBy.username }}</span>
                        <br />
                        <span>{{ scope.row.createdAt | datetimeFormat }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="最后修改" width="160">
                    <template slot-scope="scope">
                        <span>{{ scope.row.lastEditor.username }}</span>
                        <br />
                        <span>{{ scope.row.updatedAt | datetimeFormat }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="medium" title="编辑" @click="editItem(scope.row)">
                            <i class="fa fa-pencil-square-o fa-lg" style="color:blue; "></i>
                        </el-button>
                        <el-button size="medium" type="text" title="删除" @click="deleteItem(scope.row)">
                            <i class="fa fa-trash-o fa-lg" style="color:red; "></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currnetPageChange"
                layout="total, sizes, prev, pager, next, jumper"
                :total="totalCount"
                :page-sizes="[5, 10, 20, 30]"
                :page-size.sync="pageSize"
                :current-page.sync="currentPage"
            ></el-pagination>
        </el-card>

        <!-- 添加和修改版块的对话框  开始 -->
        <el-dialog
            :visible.sync="dialog"
            width="80%"
            :close-on-click-modal="false"
            :fullscreen="fullscreen"
        >
            <span slot="title">
                <el-button type="text" @click="fullscreen = true" v-if="!fullscreen">
                    <i class="fa fa-square-o" style="font-size: 20px;" title="最大化"></i>
                </el-button>
                <el-button type="text" @click="fullscreen = false" v-else>
                    <i class="fa fa-clone" style="font-size: 20px;" title="还原"></i>
                </el-button>
                <span class="dialog-title">{{ formTitle }}</span>
            </span>
            <el-card shadow="always">
                <el-form label-position="top">
                    <el-row :gutter="20">
                        <!-- :gutter="20" 同一行内，两列之间的间隔 -->
                        <el-col :md="24 - formCols" :sm="24" v-if="editedIndex !== -1">
                            <el-form-item label="置顶值">
                                <el-input
                                    type="number"
                                    v-model="editedItem.level"
                                    controls-position="right"
                                    :max="1000"
                                    label="Level"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :md="formCols" :sm="24">
                            <el-form-item label="所属版块">
                                <el-select
                                    v-model="currCategory"
                                    placeholder="请选择版块名称"
                                    style="width:100%;"
                                >
                                    <el-option
                                        v-for="item in categorySelectItems"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="标题">
                        <el-input
                            v-model="editedItem.title"
                            placeholder="标题"
                            maxlength="50"
                            show-word-limit
                        ></el-input>
                    </el-form-item>

                    <el-form-item label="主题内容(支持 Markdown 语法)">
                        <el-input
                            type="textarea"
                            v-model="editedItem.content"
                            :rows="row"
                            placeholder="主题内容"
                        ></el-input>
                    </el-form-item>
                </el-form>
            </el-card>
            <span slot="footer">
                <el-button type="danger" @click="dialog = false">取消</el-button>
                <el-button type="primary" @click="saveItem">保存</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import moment from 'moment';
import CategoryService from "../../../api/service/category_service";
import TopicService from '../../../api/service/topic_service';
import Topic from '../../../model/topic';
export default {
    name: 'TopicManager',
    data: () => ({
        categorySelectItems: [],  // 版块下拉框的数据
        currCategory: '',    // 下拉框选中的当前版块
        categories: [],      // 所有版块的原始数据
        topices: [],         // 所有的版块数据
        totalCount: 0,       // 总记录数量
        totalPage: 1,        // 总页数
        pageSize: 10,        // 每页大小
        currentPage: 1,      // 当前页
        dialog: false,       // 添加和修改对话框       
        fullscreen: false,   // 添加 修改 对话框 是否全屏
        loading: false,      // 数据表格 显示 正在加载的动画
        selected: [],        //多选框选中的项
        editedIndex: -1,     // 添加 或 修改 的标志；-1表示添加，否则为当前编辑项在 categories 中的索引
        // 添加或修改绑定到对话框上的数据
        editedItem: {
            category: {},   // 主题所属版块
            title: "",      // 主题的标题
            content: "",    // 主题的内容
            level: 0,       // 手动排序值
        },
        // 用于添加或修改结束后，还原 editedItem 的初始值       
        defaultItem: {
            category: {},
            title: "",
            content: "",
            level: 0,
        },
    }),
    mounted() {
        this.getTopices();
    },
    methods: {
        // 获取分类
        async getCategories() {
            this.categories = [];
            this.categorySelectItems = [];
            // 此处假设版块总数不大于1000
            let response = await CategoryService.fetchAll(1000, 0, [], ['-level', '-createdAt']);
            this.categorySelectItems = response.map(item => {
                return {
                    label: item.get('title'),
                    value: item.id,
                }
            });
           
            // 保留原始的 版块数据 用于添加主题时 获取相应的版块
            this.categories = response;
           

        },
        // 获取主题数据
        async getTopices() {
            this.loading = true;

            this.topices = [];
            let skip = this.pageSize * (this.currentPage - 1);
            let response = await TopicService.fetchAll(this.pageSize, skip, ["category", "createdBy", "lastEditor"], ['-level', '-updatedAt'],);
            // 数据查询成功后再查询总记录数(注意，因为是分两步完成，所以总数可能会不对)
            if (response) {
                this.totalCount = await TopicService.getTotalCount();
            }
            // 直接调用用模型类 (src/model/topic.js) 的方法处理数据：new Topic().formJson(item)
            this.topices = response.map(item => { return new Topic().formJson(item) });

            this.loading = false;
        },

        // 因为要获取最新的版块列表，所以通过函数打开添加对话框
        async createItem() {
            await this.getCategories();  // 获取最新版块列表

            /**
             * 设置下拉框的默认值为第一个
             * 下拉框默认值, 设置下拉框默认值要使用 value，
             * 否则没有改变过的下拉框结果可能会是 Object 类型
             */
            this.currCategory = this.categorySelectItems[0].value;            
            this.dialog = true;   // 打开添加对话框
        },

        // 显示修改对话框
        async editItem(item) {
            // 查找 被 编辑项的索引值
            this.editedIndex = this.topices.indexOf(item);
            // Object.assign() 用于将源对象复制到目标对象
            // 因为直接用等号赋值，这两个变量操作的是同一个内存地址
            this.editedItem = Object.assign({}, item);
            await this.getCategories();  // 获取最新版块列表

            // 下拉框默认值, 设置下拉框默认值要使用 value， 
            // 否则没有改变过的下拉框结果可能会是 Object 类型
            this.currCategory = this.editedItem.category.value;
            this.dialog = true;  // 显示编辑对话框
        },

        // 添加或修改时保存数据
        async saveItem() {
            console.log('this.editedIndex：', this.editedIndex);

            // 下拉框有 change时 版块下拉框值的类型为String，
            // 下拉框没有 change时 ，版块下拉框值的类型为Object，
            console.log('选中的版块：', typeof this.currCategory, this.currCategory);

            if (this.editedIndex === -1) {
                /**
                * 这里是添加新主题时执行的代码
                */


                /**
                 * 从 版块的 原始数据中 找出匹配的项
                 * 原始数据的每一项是一个 leancloud Class
                 * 把这个 leancloud Class 当成 topic 的一个值保存, 就会自动关联到 对应的另一个 leancloud Class
                 * category 必须是一个 leancloud Class 类型
                 * leancloud Class 类型获得的两种办法：
                 * 1、使用leancloud 的 Query 方法创建， 例如：let category = new LC.Query("Category");
                 * 2、从查询结果的原始数据中获得（这里用的是这一种） 
                 */
                let category = this.categories.find(item => { return item.id === this.currCategory });

                // 原始的  leancloud Class 数据不能直接赋值，要使用 set() 方法
                category.set('topicCount', category.get('topicCount') + 1);  // 版块中主题数量 加1, 

                // 新添加的主题， 第一方法：直接构造要添加的数据 
                // let topic = {               
                //     category: category,                // 用于关联到 版块 的字段
                //     title: this.editedItem.title,      // 主题的标题
                //     content: this.editedItem.content,  // 主题的内容
                //     views: 0,       // 查看次数                    
                //     reply: 0,       // 评论数
                //     level: 0,       // 用于手工排序的字段， 显示时，数字大的在前面
                //     createdBy: await TopicService.currentUser(),     // 创建者
                //     lastEditor: await TopicService.currentUser(),    // 最后修改者 
                // }


                /**
                 * 新添加的主题，第二种方法：通过 模型类 (src/model/topic.js) 创建要添加的数据
                 * 构造函数中的默认值不用再次赋值
                 * 构造函数没有的字段可以直接添加（动态语言的好处）
                 */
                let topic = new Topic();
                topic.category = category;
                topic.title = this.editedItem.title;
                topic.content = this.editedItem.content;
                topic.createdBy = await TopicService.currentUser();
                topic.lastEditor = await TopicService.currentUser();

                let response = await TopicService.create(topic);   // 添加一个主题
                if (response) {
                    this.getTopices();  // 更新数据, 因为需要 level 值大的项排前面，所以重新加载数据最简便
                    // 修改 相应 版块的  topicCount 值 
                    await CategoryService.update(category.id, { topicCount: category.topicCount, });
                    this.$message({
                        message: '成功添加一个新的主题',
                        type: 'success'
                    });

                    this.dialog = false;  // 关闭对话框
                } else {
                    this.$message({
                        message: '出现错误, 无法添加新主题修改',
                        type: 'error'
                    });
                }
            } else {
                /**
                 * 这里是修改时调用的代码
                 */
                let category = this.categories.find(item => { return item.id === this.currCategory });

                // 使用 Object.assign() 合并获得修改后的数据到变量 item 中，获取在对话框中已修改的内容
                let item = Object.assign(
                    this.topices[this.editedIndex],
                    this.editedItem
                );
                // 修改时，只包含有变化值的字段名
                let topic = {
                    /**
                     * 这里有一个bug, 之前每次添加一个主题时，对应版块的 topicCount +1
                     * 如果主题所属的版块也修改了，应该把所属旧版块的  topicCount -1 ，所属新版块的  topicCount -1
                     * 我不想费脑子了，所以这个有待需要的人自己去实现
                     */
                    category: category,                    // 所属类别
                    title: item.title,          // 主题的标题
                    content: item.content,      // 主题的内容                    
                    level: Number(item.level),  // 用于手工排序的字段， 显示时，数字大的在前面                  
                    lastEditor: await TopicService.currentUser(),    // 最后修改者 
                }

                let response = await TopicService.update(item.id, topic);
                if (response) {
                    // 更新 当前数据 方法一： 想省事用这个就可以了
                    //  this.getTopices();


                    // 更新 当前数据方法二： 考虑到有些网络差，或是节省流量，建设用这个
                    // 最后修改时间（updatedAt)
                    this.topices[this.editedIndex].updatedAt = response.updatedAt;
                    // 从下拉框列表中查找更新后的 版块信息
                    let result = this.categorySelectItems.find(item => {
                        return item.value === this.currCategory;
                    });
                    // 更新当前数据的 版块信息
                    this.topices[this.editedIndex].category = result;
                    // 将结果按照 level 重新排序
                    this.topices.sort((a, b) => {
                        return b.level - a.level;
                    });
                    // 更新 当前数据方法二 到这里结束 

                    this.dialog = false;   // 关闭对话框
                    this.$message({
                        message: '主题编辑成功',
                        type: 'success'
                    });
                } else {
                    this.$message({
                        message: '出现错误, 无法完成主题的修改',
                        type: 'error'
                    });
                }



            }


        },

        // 删除一条数据
        deleteItem(item) {
            // 在数组中查找删除对象的索引
            this.editedIndex = this.topices.indexOf(item);
            this.editedItem = Object.assign({}, item);  // 复制要删除的对象

            // 显示删除确认对话框
            this.$confirm(`确定要删除标题名为："${item.title}" 的主题吗？`, '删除主题', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // .then()  里面的匿名函数前要加  async 
                // 在服务器端执行删除操作
                let response = await TopicService.delete(item.id);
                if (response) {
                    // 从当前显示 的数据中删除对应的项
                    this.topices.splice(this.editedIndex, 1)[0];
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },

        // 用于表格多选框取值的函数
        mutilSelectionChange(val) {
            this.selected = val;   // 保存选中的行
            console.log(this.selected);
        },

        // 批量删除已选中的多行
        deleteSelected() { },

        // 重置选中的多个level 值为 0， 批量修改的应用
        resetSelectedLevel() { },

        // 关闭对话框时，要重置相关变量
        closeDialog() {
            this.dialog = false;
            this.fullscreen = false;
            // this.$nextTick()将回调延迟到下次 DOM 更新循环之后执行
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        // 修改每页大小时的方法
        pageSizeChange(val) {
            // 如果使用 带 .sync 修饰符的绑定，如：  :page-size.sync="pageSize" 
            // 则不需要用下一行的方式给 页大小赋值
            // this.pageSize = val;  
            this.getTopices();
        },

        // 修改当前页时的方法
        currnetPageChange(val) {
            // 如果使用 带 .sync 修饰符的绑定，如：  :current-page.sync="currentPage"
            // 则不需要用下一行的方式给 页大小赋值
            // this.currentPage = val;
            this.getTopices();

        },

    },

    // 计算属性
    computed: {
        // 添加或修改对话框的标题
        formTitle() {
            return this.editedIndex === -1 ? "添加新主题" : "主题编辑";
        },

        // 添加或修改对话框中 标题 文本框的宽度
        formCols() {
            // 添加时不用显示 Level，Title 宽度为 24
            // 修改时需要显示 Level，Title 宽度为 21
            return this.editedIndex === -1 ? 24 : 21;
        },

        // 多行文本框的行数
        row() {
            // 全屏状态时多行文本框 15 行，非全屏时 5行
            return this.fullscreen ? 15 : 5;
        }
    },

    // 过滤器
    filters: {
        // 使用 moment.js 定义日期时间过滤器函数
        datetimeFormat(val) {
            return moment(val).format("lll");
        },
    },

    // 观察属性， 函数名为变量名，当该变量值有变化时，触发函数
    watch: {
        // 监控变量 dialog, 当对话框关闭时，还原对话框相关的初始值 
        dialog(val) {
            // 相当于 if 语句的简化写法
            val || this.closeDialog();
        },
    }

}
</script>

<style>
.header-text {
    font-size: 24px;
}

.dialog-title {
    margin-left: 12px;
    font-size: 24px;
}

/* 分页组件靠右对齐 */
.el-pagination {
    text-align: right;
}
</style>

